@import '../../../node_modules/reset-css/sass/reset';
@import '../../../node_modules/open-weather-icons/dist/css/open-weather-icons.css';
@import './colors.scss';

html {
    font-family: 'Droid Sans', 'Arial Narrow', Arial, sans-serif;
}

body>.wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;
}

.hidden {
    opacity: 0;
}

.fullscreen-toggle {
    line-height: 1.4;
    text-transform: uppercase;
    position: absolute;
    display: block;
    top: 1.5vw;
    left: 1.5vw;
    z-index: 11;
    font-size: 2vw;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
    padding: 10px 20px;
    background-color: transparentize($text, 0.7);
    color: $text;
    border: 2px solid $text;
    opacity: 0;
    transition: opacity 0.5s, background-color 0.5s;

    &:hover {
        background-color: transparentize($text, 0.4);
    }
}

.shade {
    background-image: url(data:image/png;base64,);
    position: absolute;
    height: 180vh;
    width: 50%;
    transform: rotate(-20deg);
    top: -30vh;
    box-shadow: $shadow 0 0 80px;
    background-color: $initial-background-color;
    transition: background-color 0.5s;
}

.shade1 {
    transition-delay: 0.2s;
    left: -25%;
}

.shade2 {
    transition-delay: 0.4s;
    left: 14%;
}

.shade3 {
    transition-delay: 0.6s;
    left: 43%;
}

.shade4 {
    transition-delay: 0.8s;
    left: 71%;
}

.shade5 {
    transition-delay: 1s;
    left: 99%;
}

.date {
    position: absolute;
    transition: opacity 3s;
    right: 30%;
    top: 70vh;
    color: $text;
    font-size: 3vw;
    font-weight: bolder;
    text-shadow: $shadow 0 10px 60px;
}
.time {
    position: absolute;
    transition: opacity 3s;
    right: 30%;
    bottom: 30vh;
    color: $text;
    font-size: 23vw;
    font-weight: bold;
    text-shadow: $shadow 0 10px 60px;
}

.weather {
    position: absolute;
    right: 4%;
    bottom: 30vh;
    margin-bottom: 35px;
    display: flex;
    justify-content: end;
    flex-direction: column;
    color: $text;
    transition: opacity 3s;

    .owi {
        font-size: 5vw;
        font-weight: bolder;
    }

    .temperature {
        font-size: 8vw;
        margin-left: 25px;
    }

    .description {
        font-size: 2vw;
    }

    .location {
        font-size: 2vw;
        font-weight: bold;
        margin-left: 5px;
    }

    .row1, .row2 {
        text-align: right;
    }
}

@import './media_portrait.scss';
